body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.UploadImageContainer {
    text-align: center;
    max-height: 100%;
    overflow-y: scroll;
}

.PhotoItem {
    padding: 5px;
    max-width: 250px;
  }

.PhotoGrid {
    max-width: 100%;
    grid-gap: 10px;
    text-align: center;
    display: grid;
    grid-template-columns: repeat(4, auto);
    align-items: center;
    justify-items: center;
  }
.Timeline {
    width: 100px;
    float: right;
  }
  
.Map {
    width: 100px;
    float: left;
  }

.PhotoTrail {
    width: 100%;
    height: 100%;
    display: inline-block;
    overflow: hidden;
    border: 1px solid black;
}

.NavBar {
    background: black;
    width: 100%;
    height: 4%;
    position: fixed;
    top: 0px;
    display: flex;
    align-items: center;
}


li {
    display: inline-block;
    padding-right: 60px;
    margin-left: auto;

}

.Link {
    color: white;
    text-decoration: none;
    margin-left: auto;
}
